import { mapActions, mapGetters, mapState } from "vuex";
import Category from "../components/Category.vue";

export default {
  data() {
    return {};
  },
  methods: {
    ...mapActions(["addProduct", "routerToDetail"]),
    toProductList: function () {
      this.$router.push("productlist");
    },
  },
  computed: {
    ...mapGetters(["homeProduct"]),
    ...mapState(["allProduct"]),
  },
  components: {
    Category,
  }
};
